import React from 'react';
import './Home.css';

class Home extends React.Component {
  state = {
    search: ""
  };
  doChange = e => {
    const newSearch = e.target.value;
    this.setState({search: newSearch});
  };
  doSubmit = e => {
    e.preventDefault();
    const {search} = this.state;
    this.props.history.push({
      pathname: "/summoner/" + search,
      state: {
        nickname: search
      }
    });
  };
  render() {
    const {search} = this.state;
    return (
      <section className="home_container">
        <div className="home_title">
          <span className="home_title_text">Troll.gg</span>
        </div>
        <div className="home_search">
          <form className="home_search_container" onSubmit={this.doSubmit}>
            <input className="home_search_input" type="text" value={search} placeholder="닉네임을 입력하세요." onChange={this.doChange} />
            <input className="home_search_submit" type="submit" value="Troll" />
          </form>
        </div>
      </section>
    );
  }
}

export default Home;