import React from 'react';
import './Summary.css';
import Summoner from '../components/Summoner';
import axios from 'axios';
import {riot_api_key, riot_summoner_info, riot_rank_info} from '../api';

class Summary extends React.Component {
  state = {
    isLoading: true,
    nickname: "",
    infos: []
  };
  async componentDidMount() {
    const {location, history} = this.props;
    if (location.state) {
      const summoner_info = await axios.get(riot_summoner_info + location.state.nickname + "?api_key=" + riot_api_key);
      console.log(summoner_info.data);
      const rank_info = await axios.get(riot_rank_info + summoner_info.data.id + "?api_key=" + riot_api_key);      
      this.setState({
        isLoading: false,
        nickname: location.state.nickname, 
        infos: rank_info.data
      });
    } else {
      history.push("/");
    }
  }
  render() {
    const {isLoading, infos, nickname} = this.state;
    if (isLoading){
      return null;
    } else {
      return (
        <div className="summary_container">
          {infos.length > 0 && infos.map((info, index) => (
            <Summoner 
              key={index} type={info.queueType}
              tier={info.tier} rank={info.rank} leaguePoints={info.leaguePoints} 
              nickname={nickname} wins={info.wins} losses={info.losses}
            />
          ))}
          {infos.length === 0 ? (
            <span>{nickname}님은 이번 시즌 랭크를 플레이하지 않았습니다.</span>
          ) : null}
          <span>딱 봐도 넌 트롤이다 ㅋ.</span>
        </div>
      );
    };
  }
}

export default Summary;