import React from 'react';
import PropTypes from 'prop-types';
import './Summoner.css';
import IRON from '../images/IRON.png';
import BRONZE from '../images/BRONZE.png';
import SILVER from '../images/SILVER.png';
import GOLD from '../images/GOLD.png';
import PLATINUM from '../images/PLATINUM.png';
import DIAMOND from '../images/DIAMOND.png';
import MASTER from '../images/MASTER.png';
import GRANDMASTER from '../images/GRANDMASTER.png';
import CHALLENGER from '../images/CHALLENGER.png';
import DEFAULT from '../images/DEFAULT.png';

function rank_to_photo (rank) {
  if (rank === "IRON") return IRON;
  else if (rank === "BRONZE") return BRONZE;
  else if (rank === "SILVER") return SILVER;
  else if (rank === "GOLD") return GOLD;
  else if (rank === "PLATINUM") return PLATINUM;
  else if (rank === "DIAMOND") return DIAMOND;
  else if (rank === "MASTER") return MASTER;
  else if (rank === "GRANDMASTER") return GRANDMASTER;
  else if (rank === "CHALLENGER") return CHALLENGER;
  else return DEFAULT;
}
function type_to_string (type) {
  if (type === "RANKED_SOLO_5x5") return "솔로 랭크";
  else return "자유 랭크";
}
function Summoner({tier, type, rank, leaguePoints, nickname, wins, losses}) {
  return (
    <div className="summoner_container">
      <img src={rank_to_photo(tier)} alt={tier} title={tier} />
      <div className="summoner_info">
        <span className="summoner_nickname">{nickname}</span>
        <span className="summoner_type">{type_to_string(type)}</span>
        <div className="summoner_tierinfo">
          <span>{tier}</span>
          <span>{rank}</span>
          <span>{leaguePoints}LP</span>
        </div>
        <div className="summoner_winloseinfo">
          <span>{wins + losses} Games</span>
          <span>{wins}W</span>
          <span>{losses}L</span>
          <span>({wins + losses > 0 ? (100.00 * wins / (wins + losses)).toFixed(2) : 0.00}%)</span>
        </div>
      </div>
    </div>
  );
}

Summoner.propTypes = {
  tier: PropTypes.string.isRequired,
  type: PropTypes.string.isRequired,
  rank: PropTypes.string.isRequired,
  leaguePoints: PropTypes.number.isRequired,
  nickname: PropTypes.string.isRequired,
  wins: PropTypes.number.isRequired,
  losses: PropTypes.number.isRequired
};

export default Summoner;